.room-setting-list {
   position: absolute;
   z-index: 1;
   top: 100%;
   left: 100%;
   box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
      rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
   border-radius: 10px;
   background-color: #fff;
   display: flex;
   flex-direction: column;
   white-space: nowrap;
   overflow: hidden;
}
